.notesWrapper {
    grid-template-areas: "b"
        "c";
    display: grid;
    margin: 0 5px 5px 5px;
    overflow: hidden;
    text-overflow: ellipsis;
}



.noteWrapper button {
    padding: 0px;
    width: 95%;
    border: 0;
    text-align: left;
    border-radius: 10px;
}

.notesName {
    border-radius: 10px 10px 0px 0px;
    font-weight: bold;
    grid-area: b;
    background-color: #99B5C5;
    padding: 5px;
    max-height: 13px;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.notesText {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    padding: 5px;
    border-radius: 0 0 10px 10px;
    grid-area: c;
    background-color: rgb(255, 255, 255);
    overflow: hidden;
    text-overflow: ellipsis;
    max-height: 42px;
}