.App {
  background-color: #707070;
}

.App-wrapper{
  width: 1200px;
  height: 950px;
  display: grid;
  grid-template-columns: 15fr 85fr;
  grid-gap: 10px;
  margin: 0 auto;
  padding: 50px;
}

.Edit-wrapper{
  height: 950px;
  display: grid;
  grid-template-rows: 10fr 200fr 10fr;
  grid-gap: 5px;
  border-radius: 20px;
  background-color: #9A9A9A;
  }
  
  .List-wrapper{
    height: 950px;
    display: grid;
    grid-template-rows: 10fr 210fr ;
    grid-gap: 10px;
   
    border-radius: 20px;
    background-color: #D9D9D9;

}





